<template>
	<view v-if="showProgress">
		<u-modal 
		v-model="show" 
		:title="i18n.downloading" 
		show-cancel-button
		@cancel="handleCancel"
		:show-confirm-button="false" 
		:cancel-text="i18n.cancelText"
		:title-style="{fontSize: '32rpx', fontWeight: 'bold'}">
			<view class="line-progress-box">
				<view class="line-progress">
					<u-line-progress
					:striped="true" 
					:striped-active="true" 
					active-color="#00a6e2" 
					:percent="percent">
					</u-line-progress>
				</view>
			</view>
		</u-modal>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				show: true,
				percent: 0,
				// 显示进度条
				showProgress: false
			}
		},
		mounted() {
			// 监听是否显示下载进度条
			uni.$on('showProgress', () => {
				this.showProgress = true
			})
			// 监听载进度
			uni.$on('changeProgress', (data) => {
				this.percent = data.progress
			})
		},
		computed: {
			i18n() {
				return this.$t('dowmloadProgress')
			}
		},
		methods: {
			// 点击取消
			handleCancel() {
				uni.$emit('abortProgress')
			}
		}
	}
</script>

<style lang="scss" scoped>
	.line-progress-box{
		display: flex;
		justify-content: center;
		margin: 20rpx 0;
		.line-progress{
			width: 80%;
		}
	}
</style>
